<template>
  <div id="app">
    <label for>
      经度
      <input v-model.number="center.lng" />
    </label>
    <label for>
      纬度
      <input v-model.number="center.lat" />
    </label>
    <label for>
      缩放
      <input v-model.number="zoom" />
    </label>
    <label for>
      起点
      <input v-model="start" />
    </label>
    <label for>
      终点
      <input v-model="end" />
    </label>

    <baidu-map
      class="bm-view"
      :center="center"
      :zoom="zoom"
      ak="fuSA4L40AzEaplSwLp5Z5AkNPkSebPyS"
      :scroll-wheel-zoom="true"
      @ready="handler"
      @moving="syncCenterAndZoom"
      @moveend="syncCenterAndZoom"
      @zoomend="syncCenterAndZoom"
    >
      >
      <!-- 比例尺 -->
      <bm-scale anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-scale>
      <!-- 缩放 -->
      <bm-navigation anchor="BMAP_ANCHOR_BOTTOM_RIGHT"></bm-navigation>
      <!-- 地图类型 -->
      <bm-map-type
        :map-types="['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']"
        anchor="BMAP_ANCHOR_TOP_LEFT"
      ></bm-map-type>
      <!-- 定位 -->
      <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_LEFT" :showAddressBar="true" :autoLocation="true"></bm-geolocation>
      <!-- 全景 -->
      <bm-panorama></bm-panorama>
      <!-- 点 -->
      <bm-marker
        :position="{lng: center.lng, lat: center.lat}"
        :dragging="true"
        animation="BMAP_ANIMATION_BOUNCE"
      >
        <bm-label
          content="当前中心点"
          :labelStyle="{color: 'red', fontSize : '14px'}"
          :offset="{width: -30, height: 30}"
        />
      </bm-marker>
      <bm-transit v-if="flag.transit" :start="start" :end="end" :auto-viewport="true" location="上海"></bm-transit>
      <bm-walking :start="start" :end="end" :auto-viewport="true" location="上海"></bm-walking>

    </baidu-map>
  </div>
</template>

<script>
import BaiduMap from "vue-baidu-map/components/map/Map.vue";
import BmScale from "vue-baidu-map/components/controls/Scale.vue";
import BmNavigation from "vue-baidu-map/components/controls/Navigation.vue";
import BmMapType from "vue-baidu-map/components/controls/MapType.vue";
import BmGeolocation from "vue-baidu-map/components/controls/Geolocation.vue";
import BmPanorama from "vue-baidu-map/components/controls/Panorama.vue";
import BmMarker from "vue-baidu-map/components/overlays/Marker.vue";
import BmTransit from "vue-baidu-map/components/search/Transit.vue";
import BmWalking from "vue-baidu-map/components/search/Walking.vue";
import BmLabel from "vue-baidu-map/components/overlays/Label.vue";

export default {
  name: "App",
  components: {
    BaiduMap,
    BmScale,
    BmNavigation,
    BmMapType,
    BmGeolocation,
    BmPanorama,
    BmMarker,
    BmTransit,
    BmLabel,
    BmWalking,
  },
  data() {
    return {
      flag: {
        transit: true,
        walking: false,
      },
      center: { lng: 0, lat: 0 },
      zoom: 3,
      start: "上海东方明珠",
      end: "上海金茂大厦",
    };
  },
  methods: {
    //初始化中心
    handler({ BMap, map }) {
      console.log(BMap, map);
      this.center.lng = 116.404;
      this.center.lat = 39.915;
      this.zoom = 15;
    },
    syncCenterAndZoom(e) {
      const { lng, lat } = e.target.getCenter();
      this.center.lng = lng;
      this.center.lat = lat;
      this.zoom = e.target.getZoom();
    },
  },
};
</script>

<style>
.bm-view {
  width: 100%;
  height: 100vh;
}
.BMap_cpyCtrl {
  display: none;
}
.anchorBL img {
  display: none;
}
input {
  outline: none;
  border: none;
}
</style>
